<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图区域 -->
    <el-card>
      <!-- 搜索框和添加商品按钮区域 -->

      <el-row :gutter="20">
        <el-col :span="9">
          <!-- 搜索框区域 -->
          <el-input
            v-model="paramsQuery.query"
            placeholder="请输入内容"
            clearable
            @clear="getGoodsList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getGoodsList"
            ></el-button> </el-input
        ></el-col>
        <!-- 添加用户按钮 -->
        <el-col :span="3">
          <el-button type="primary" @click="addGoods">添加商品</el-button>
        </el-col>
      </el-row>

      <!-- 商品列表table区域 -->
      <el-table :data="goods" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="商品名称" prop="goods_name"></el-table-column>
        <el-table-column
          label="商品价格（元）"
          prop="goods_price"
          width="95px"
        ></el-table-column>
        <el-table-column
          label="商品重量"
          prop="goods_weight"
          width="75px"
        ></el-table-column>
        <el-table-column label="创建时间" prop="add_time" width="140px">
          <template slot-scope="scope">
            <!-- 过滤时间 -->
            {{ scope.row.add_time | dateFormat }}
          </template>
        </el-table-column>
        <!-- 操作栏 -->
        <el-table-column label="操作" width="130px">
          <template slot-scope="scope">
            <!-- 编辑按钮 -->

            <el-tooltip
              effect="dark"
              content="编辑"
              placement="top"
              :enterable="false"
            >
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="editGoodsDialog(scope.row.goods_id)"
              ></el-button>
            </el-tooltip>

            <!-- 删除按钮 -->

            <el-tooltip
              effect="dark"
              content="删除"
              placement="top"
              :enterable="false"
            >
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="deleteGoodsConfirm(scope.row.goods_id)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页区域 -->

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="paramsQuery.pagenum"
        :page-sizes="[5, 10, 20, 40]"
        :page-size="paramsQuery.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 商品列表
      goods: [
        {
          goods_name: '',
          goods_price: 0,
          goods_weight: 0,
          add_time: 0
        }
      ],
      // 获取商品列表的请求参数
      paramsQuery: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      // 商品总数
      total: 0
    }
  },
  created () {
    this.getGoodsList()
  },
  methods: {
    async getGoodsList () {
      const { data: res } = await this.$axios.get('goods', {
        params: this.paramsQuery
      })
      if (res.meta.status !== 200) { return this.$message.error('获取商品列表失败') }
      this.total = res.data.total
      this.goods = res.data.goods
    },

    // 显示修改商品对话框
    editGoodsDialog (id) {},
    // 删除商品确认框
    async deleteGoodsConfirm (id) {
      const rel = await this.$confirm(
        '此操作将永久删除该商品, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(err => err)
      if (rel !== 'confirm') return this.$message.info('已取消删除')
      const { data: res } = await this.$axios.delete(
        `goods/${id}`
      )
      if (res.meta.status !== 200) return this.$message.error('删除商品失败')
      this.$message.success('删除商品成功')
      this.getGoodsList()
    },

    // 单页显示数量变化的事件
    handleSizeChange (size) {
      this.paramsQuery.pagesize = size
      this.getGoodsList()
    },
    // 页码变化的事件
    handleCurrentChange (num) {
      this.paramsQuery.pagenum = num
      this.getGoodsList()
    },
    // 添加商品按钮触发的事件
    addGoods () {
      this.$router.push('goods/add')
    }
  }
}
</script>

<style lang="less" scoped></style>
